.logo {
    width: 200px;
    flex-grow: 1;
    margin: 18px 0;
}

.home {
    width: 80%;
    margin: 20px 10%;
}

.input {
    width: 250px !important;
    margin: 5px !important;
}

.textarea {
    width: calc(100% - 10px) !important;
    min-width: 250px !important;
    margin: 5px !important;
}

.sortby {
    display: flex;
    position: absolute;
    left: 5%;
    top: 100px;
}

.container {
    flex-direction: column;
    margin-top: 70px;
}

.job_container {
    width: 100%;
    max-width: 700px;
    padding: 20px;
    margin: 5px 0;
    cursor: pointer;
}

.job_container:hover {
    box-shadow: 0 2px 3px 0 rgb(0 106 194 / 20%);
}

.job_title {
    font-weight: 500;
    font-size: 18px;
    margin: 0;
}

.job_company {
    font-size: 14px;
    margin: 5px 0;
}

.job_details {
    display: flex;
    margin: 5px 0;
}

.job_details span {
    margin: 0 10px 0 5px;
}

.job_description {
    display: flex;
    margin: 5px 0;
}

.job_description span {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

@media only screen and (max-width: 600px) {
    .form_inputs {
        display: flex;
        flex-direction: column;
    }
}
